<template>
  <div class="cardPage">
		<div>
			<search-bar :search="searchCards" v-slot="{searchResults}" class="searchbar">
					<li class="result" v-for="r of searchResults" :key="r.id" @click="select(r)">
						<span class="cardName grow white">{{r.name}}</span>
						<img :src="r.imageUrl" alt="" class="cardImg">
					</li>
			</search-bar>
		</div>
		<div class="selectedCards">
			<ul v-for="card of this.selectedCards" :key="card.name" >
				<li>{{card.name}}</li>
				<img :src="card.imageUrl" />
			</ul>
		</div>

	</div>
</template>

<script>


export default {
  name: 'mtg',
    components: {},
    beforeCreate() {
        this.$options.components.HelloWorld = require('@/components/HelloWorld.vue').default;
				this.$options.components.SearchBar = require('@/utilities/SearchBar.vue').default;
				this.$options.components.test = require('@/utilities/test.vue').default;
    },
  data() {
		return{
			q: '',
			searchResults: null,
			loading: 0,
			mtg: '',
			mtgName: '',
			cards: [],
			selectedCards: []
		}
  },
  components: {
  },
	created() {
		this.getCards('');
	},
  methods: {
		async searchCards(q) {
			var r = ''
			if(q.length < 3) {
				return
			}
			await fetch(`https://api.magicthegathering.io/v1/cards?name=${q}`)
				.then(response => response.json())
				.then(data => r = data)
			console.log(`rrrrr: ${JSON.stringify(r.cards)}`)
			return r.cards
		},


		async getCards(name){
			let mtgData = ''
			console.log("HERE WE GO")
			await fetch('https://api.magicthegathering.io/v1/cards?name=' + name)
				.then(response => response.json())
				.then(data => mtgData = data)
				console.log(mtgData)
				console.log("FIRE IN THEE HOOL")
				let cards = mtgData.cards
				console.log(cards.length)
				
				let newcards = cards.sort( function ( a,b ) {
					a = a.name.toLowerCase();
					b = b.name.toLowerCase();

					return a < b ? -1 : a > b ? 1 : 0;
				});
				console.log(newcards)
				if(cards.length > 0){
					this.cards = newcards
					this.mtg = newcards[0]
				} else {
					this.mtg = ''
				}
		},
		async select(card){
			console.log(card)
			this.selectedCards.push(card)
		},
		async yeah(){
			console.log("yeah")
		}
  }
}
</script>
<style scoped>
.selectedCards{
	display: flex;
}
.cardPage{
	display: grid;
	grid-template-columns: 1fr 2.5fr;
}
.filters {
	display: grid;
	grid-template-columns: 1fr 1fr;
	margin-bottom: 1rem;
}

.cardName{
	margin-top: 2rem;
}

.cardImg{
	max-height: 20rem;
}

.searchbar{
	min-width: 100%;
	max-height: 300px;
}

.result{
	background: #3d6ea1;
	display: flex;
	padding-left: .5rem;
	padding-top: 2px;
	padding-bottom: 2px;
}

.result:hover {
	background: var(--accent);
	color: gray;
}
</style>