<template>
  <div class="autocomplete">
    <span class="search-flex">
      <input id="search" type="text" placeholder="Search by name" v-model="q" autocomplete="off" @blur="close()">
    </span>
    <ul class="results" v-if="searchResults && searchResults.length">
      <slot :search-results="searchResults"/>
    </ul>
    <ul v-else-if="searchResults && searchResults.length === 0" class="results">
      <li class="result">No results</li>
    </ul>
  </div>
</template>

<script>
export default {
  components: {
  },
  data() {
    return {
      q: '',
      searchResults: null,
      loading: 0
    }
  },
  props: ['search'],
  watch: {
    q() {
			console.log("QQQQQQQQQQ")
      clearTimeout(this.searchTimeout)
      this.searchTimeout = setTimeout(() => {
        if (!this.q) {
          return this.searchResults = null
        }
        this.loading++
        this.search(this.q).then(results => {
          this.loading--
          this.searchResults = results
					console.log(this.searchResults)
        })
      }, 500)
    }
  },
  methods: {
    close() {
      // hide the search results when the user clicks outside of them
      setTimeout(() => {this.searchResults = null}, 200)
    }
  }
}
</script>
<style scoped>
.autocomplete {
  position: relative;
  width: 100%;
  margin-right: 0;
}
.autocomplete input {
  width: 100%;
  margin-bottom: 0;
  padding-right: 0;
  margin-right: 0;
}
.results {
  position: absolute;
  z-index: 2;
  box-sizing: border-box;
  width: 100%;
  margin: 0;
  list-style: none;
  border: 1px solid #cccccc;
  border-top: none;
  cursor: pointer;
  padding: 0 0 3px 0;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
	max-height: 75vh;
	overflow: scroll;
}

.result{
	background: #518fd1;
	display: flex;
	padding-left: .5rem;
	padding-top: 2px;
	padding-bottom: 2px;
}

.result:hover {
	background: var(--accent);
	color: gray;
}
</style>